<template>
    <div>
        <button id="captureBtn" @click="capture" style="position:fixed;bottom:10px;left:30px;z-index:100;font-size:20px;" class="btn btn-lg rounded-circle btn-success text-lg pt-3 pb-2" >
            <i class="text-lg fa fa-camera" style="font-size:20px;" aria-hidden="true"></i></button>
    </div>
</template>
<script>
export default{
    data(){
        return{
            modal1:false
        }
    },
    methods: {
        capture(){
            alert('woke');
        }
    },
    layout:'camera',
    mounted() {
        if (typeof cordova == "object") {
            document.addEventListener("deviceready", function() {
                let options = {
                    x: 0,
                    y: 0,
                    width: window.screen.width,
                    height: window.screen.height,
                    camera: CameraPreview.CAMERA_DIRECTION.BACK,
                    toBack: false,
                    tapPhoto: true,
                    tapFocus: false,
                    previewDrag: false,
                    storeToFile: false,
                    disableExifHeaderStripping: false
                };
                CameraPreview.startCamera(options);
               
            });
        }
    },      
}
</script>